* {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  /* font-weight: ; */
  font-style: normal;
  font-variation-settings: "slnt" 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


.color-app {
  color: #9e2a53;
}

.back-to-top {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background-color: #9e2a53;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 100;
  transition: background-color 0.3s ease;
  font-size: 26px;
}

.back-to-top:hover {
  background-color: #9e2a53;
  cursor: pointer;
  /* font-size: 24px; */
}
.product-rating {
  color: rgb(60, 181, 0);
  background: rgb(221, 243, 221);
  display: inline;
  padding: 4px;
  font-size: 12px;
}

.card-img-top {
  height: 180px;
  object-fit: cover;
}
.product-info {
  display: flex;
  align-items: center;
  border-top: 0.5px solid #807d7d60;
  padding: 10px 15px;
}

.product-price {
  margin-right: 5px;
  color: #9e2a53;
  font-weight: bold;
}

.product-off-price {
  text-decoration: line-through;
  margin-right: 10px;
}

.product-discount-box {
  background-color: #369d00;
  color: #fff;
  padding: 2px;
}

.product-add-to-cart {
  font-weight: 600;
  position: relative;
  border: 1px solid #fff;
  box-shadow: none;
  border-radius: 4px;
  transform: scale(1);
  transition: all 0.2s ease-in-out 0s;
  text-align: center;
  flex-shrink: 0;
  color: #fff;
  background-color: #9e2a53;
  padding: 4px 12px 6px;
  font-size: 0.9rem !important;
  -webkit-box-align: center !important;
  align-items: center !important;
}

.buy-now-button {
  background-color: #9e2a53;
  color: #fff;
  text-align: center;
  padding: 10px;
  width: 45%;
}

.add-to-cart-button {
  color: #9e2a53;
  border: #9e2a53 solid 1px;
  text-align: center;
  padding: 10px;
  width: 45%;
}

.buy-now-button:hover {
  cursor: pointer;
}

.add-to-cart-button:hover {
  cursor: pointer;
}

.col-md-6 a {
  color: #000;
}

.col-md-6 a:hover {
  color: #000;
}

.order-container {
  width: 200px;
  min-height: 230px;
  box-sizing: border-box;
}

.color-black {
  color: #000;
}

.color-white {
  color: #fff !important;
}
.color-white:hover {
  color: #fff !important;
}

.user-info {
  display: flex;
}

.user-info-img {
  width: auto;
  height: auto;
}

.user-info-details {
  padding: 5px;
}

.dropdown-btn {
  background-color: #9e2a53;

  color: #fff;
  /* padding: 16px; */
  font-size: 20px;
  border: none;
}

.dropdown {
  position: relative;
  display: inline-block;
}

