.dropdown-menu a {
  display: block; /* Display links as block elements */
  padding: 0.25rem 1.5rem; /* Add padding */
  clear: both; /* Clear floats */
  font-weight: 400; /* Set font weight */
  color: #212529; /* Set text color */
  text-align: inherit; /* Inherit text alignment */
  white-space: nowrap; /* Prevent wrapping */
  background-color: transparent; /* Set background color */
  border: 0; /* Remove border */
  text-decoration: none; /* Remove underline */
}

.dropdown-menu a:hover {
  color: #fff; /* Change text color on hover */
  background-color: #007bff; /* Change background color on hover */
}


.dropdown:hover .dropdown-menu {
  display: block;
}

.border-app {
  border: #9e2a53 solid 1px;
}

.terms-list > li {
  list-style-position: inside;
  list-style-type: disc;
}

.delete-btn {
  background-color: transparent; /* Default background color */
  color: #212529; /* Default text color */
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease; /* Transition effect for background-color and transform */
}

.delete-btn:hover {
  background-color: transparent; /* Background color on hover */
  color: #dc3545; /* Text color on hover */
  transform: scale(1.3); /* Scale effect on hover */
}


.terms-list > li {
  list-style-position: inside;
  list-style-type: disc;
}

.contact-us-background {
  /* background-image: url("/public/images/contact-us-background.jpg"); */
  background-position: center;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 250px;
}

.contact-form {
  position: relative;
  top: -50px;
  display: flex;
  margin: 10px;
  padding: 10px;
  padding-top: 3rem;
  flex-direction: column;
  background: #fff;
}

.contact-form input {
  display: flex;
  padding: 8px 14px;
  align-items: center;
  gap: 8px;
  align-self: stretch;
  border-radius: 8px;
  border: 1px solid #dbe6f2;
  background: var(--white);
  box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
  overflow: hidden;
  color: #3f5374;
  text-overflow: ellipsis;
  line-height: 24px;
}

.contact-form-background {
  background: #f8f4f4;
}

.contact-form-heading {
  font-size: 2.5rem;
  font-weight: 600;
}

.contact-details > a {
  color: #fff;
}

.mail-and-tel > a:hover {
  color: #fff !important;
}

.contact-details svg {
  margin: 5px;
}

.contact-us-links {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

.contact-us-links hr {
  color: #000;
}

.contact-us-links a {
  color: #000;
}

.contact-us-links ul {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-us-links > div {
  margin: 5px;
}

.contact-us-button {
  background-image: linear-gradient(to bottom right, #9e2a53, #ffd700);
  color: #fff;
  width: 150px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;

  margin: 20px;
}

.contact-us-button:hover {
  background-image: linear-gradient(to bottom right, #ffd700, #9e2a53);
}

.contact-error-message {
  margin: 20px;
  text-align: start;
  font-size: 24px;
  color: red;
}

.input-box {
  height: 100%;
  border: 1px solid #000;
  border-radius: 3px;
  margin: 2px;
  padding: 5px;
}

.input-box-text-area {
  height: 100%;
  border: 1px solid #000;
  border-radius: 3px;
  margin: 2px;
  padding: 5px;
  width: 100%;
}
 