.ForgotPasswordContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
    background-color: #f8f9fa;
  }
  
  .ForgotPasswordBox {
    width: 400px;
    padding: 20px;
    background: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  
  .ForgotPasswordBox h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .emailInput input {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .submitButton {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .submitButton:hover {
    background-color: #0056b3;
  }
  
  .error {
    color: red;
    text-align: center;
  }
  
  .success {
    color: green;
    text-align: center;
  }
  