.notifications-icon-container{
    position: relative;
}
.notifications-count{
    position: absolute;
    top: 1px;
    background-color:  rgb(187, 6, 6);
    color: white;
    font-weight: bolder;
    font-size: 10px;
    padding: 1px 5px 1px 5px ;
    border-radius: 20px;
}

.header-search {
    position: relative;
 }
 .header-search .dropdown-menu {
   display: block; 
   position: absolute;
   top: 100;
   left: 0;
   width: 100%;
   z-index: 999;
   background-color: white; 
   border: 1px solid #ced4da; 
   border-radius: 0.25rem; 
 }
 
 .header-search .form-control {
   position: relative;
 }
 
 .header-search .dropdown-menu.show {
   display: block;
   top:40px
 }

.header .dropdown-item {
  display: flex;
  align-items: center;
  padding: 10px 15px;

}

.header .dropdown-item i {
  margin-right: 8px;
}

.header .dropdown-menu {
  padding: 0;
  border: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
.header .dropdown-menu-left {
  position: absolute;
  top: 100%;
  left: -30%;
}

.header .dropdown-menu .dropdown-item {
  padding: 10px 15px;
}
.header .dropdown-menu .dropdown-item:hover {
  background-color: #f8f9fa;
}
.header .dropdown-toggle::after {
  display: none;
}

.header .navbar-nav .nav-item {
  margin-left: 10px;
}

.header .navbar-nav .nav-link {
  display: flex;
  padding: 0.5rem 1rem;
}

.header .header-search {
  width: 100%;
}

.header .navbar-toggler {
  border: none;
  padding: 0.25rem 0.75rem;
}


@media (max-width: 576px) {
  .header .navbar-nav.ms-auto {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .header .dropdown-menu{
    position: absolute !important;
    left: 30;
  }
  .header .navbar-nav {
    /* flex-direction: row; */
    /* justify-content: space-between; */
    width: 100%;
  }

  .header .header-search {
    flex: 1;
    margin-right: 10px;
  }
  .header .nav-item {
    flex: 0 0 auto;
  }
  .header .navbar-nav .nav-item {
    margin-left: 5px;
    margin-right: 5px;
  }

  .header .navbar-nav .nav-item .nav-link {
    padding: 0.5rem;
  }
}
